body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f1f3f5;
  line-height: 1.8;
}
a {
  color: #845ef7; }
  a:visited {
    color: #845ef7; }

.page-container {
  max-width: 728px;
  margin: 0 auto;
  padding-top: 3rem;
  padding-bottom: 10rem; }
  .page-container h1 {
    font-size: 3rem; }
  .page-container .image {
    width: 60px;
    height: 60px;
    background-image: url(/static/media/Image.3543e9c9.jpeg);
    background-size: cover;
    border-radius: 50%;
    margin-right: 2rem; }
  .page-container .section {
    margin-top: 4rem; }
    .page-container .section .section__title {
      font-size: 1.7rem; }
    .page-container .section .flex {
      display: flex; }
    .page-container .section .profile__list {
      flex: 1 1;
      width: 100%;
      margin: 0;
      padding: 0;
      list-style: none; }
    .page-container .section .profile__name {
      font-weight: bold;
      font-size: 1.3rem; }
    .page-container .section .profile__meta {
      margin-top: 0.7rem; }
    .page-container .section .profile__intro {
      margin-top: 1rem; }

.education-container dt {
  color: grey; }

.education-container dd {
  padding: 0;
  margin: 0; }

.tech-stack-container dl {
  display: flex;
  margin-top: 0;
  margin-bottom: 2rem; }

.tech-stack-container dt {
  color: grey;
  margin: 0;
  padding: 0;
  min-width: 120px; }

.tech-stack-container dd {
  margin: 0;
  padding: 0; }

