a {
  color: #845ef7;
  
  &:visited {
    color: #845ef7;
  }
}

.page-container {
  max-width: 728px;
  margin: 0 auto;
  padding-top: 3rem;
  padding-bottom: 10rem;

  h1 {
    font-size: 3rem;
  }
  .image {
    width: 60px;
    height: 60px;
    background-image: url('./Image.jpeg');
    background-size: cover;
    border-radius: 50%;
    margin-right: 2rem;
  }
  .section {
    margin-top: 4rem;

    .section__title {
      font-size: 1.7rem;
    }
    .flex {
      display: flex;
      
    }
    .profile__list {
      flex: 1;
      width: 100%;
      margin: 0;
      padding: 0;
      list-style: none;
    }
    .profile__name {
      font-weight: bold;
      font-size: 1.3rem;
    }
    .profile__meta {
      margin-top: 0.7rem;
    }
    .profile__intro {
      margin-top: 1rem;
    }
    
  }
}

.education-container {
  dt {
  color: grey;
}
dd {
  padding: 0;
  margin: 0;
}
}

.tech-stack-container {
  dl {
    display: flex;
    margin-top: 0;
    margin-bottom: 2rem;
  }
  dt {
    // font-weight: bold;
    color: grey;
    margin: 0;
    padding: 0;
    min-width: 120px;
  }
  dd {
    margin: 0;
    padding: 0;
  }
}